import "./homework_13.scss";

/////
const xhr = new XMLHttpRequest();
const body = document.querySelector("body");
const mountPoint = document.createElement("div");
mountPoint.classList.add("slider-wrapper");
body.appendChild(mountPoint);
const btnBox = document.createElement("div");
btnBox.classList.add("btn_box");
mountPoint.appendChild(btnBox);
const NButton = document.createElement("button");
const PButton = document.createElement("button");
NButton.classList.add("btn__Next");
PButton.classList.add("btn__Back");
btnBox.appendChild(NButton);
btnBox.appendChild(PButton);
NButton.textContent = "<<";
PButton.textContent = ">>";

const loadHandler = () => {
  const posts = JSON.parse(xhr.response);
  for (let post of posts) {
    const img = document.createElement("img");
    const containerRoot = document.createElement("div");
    img.classList.add("slide");
    img.src = post.img;
    containerRoot.appendChild(img);
    containerRoot.classList.add("slider");
    mountPoint.appendChild(containerRoot);
    console.log(post.img);
  }
};

xhr.open("GET", "http://localhost:3000/movies");
xhr.send();

xhr.addEventListener("load", loadHandler);

////////
const container = document.querySelector(".slider-wrapper");
const blocks = container.querySelectorAll(".slide");
const nextButton = document.querySelector(".btn__Next");
const prevButton = document.querySelector(".btn__Back");
let activeIndex = 0;
console.log(blocks.length);

/////
const toggleOff = () => {
  for (let i = 0; i < blocks.length; i++) {
    blocks[i].classList.remove("active");
  }
};
const activeBlock = () => {
  blocks[activeIndex].classList.add("active");
};
const changeIndexRight = () => {
  if (activeIndex + 1 < blocks.length) {
    activeIndex += 1;
  } else {
    activeIndex = 0;
  }
};
const changeIndexLeft = () => {
  if (activeIndex - 1 > -1) {
    activeIndex -= 1;
  } else {
    activeIndex = 2;
  }
};
const toggleClickRight = () => {
  toggleOff();
  changeIndexRight();
  activeBlock(0);
};
const toggleClickLeft = () => {
  toggleOff();
  changeIndexLeft();
  activeBlock(0);
};
nextButton.onclick = toggleClickRight;
prevButton.onclick = toggleClickLeft;
